<template>
  <div class="animated slideInLeft" style="animation-duration:100ms">
  	<b-card id="studentform">
  			<b-tabs>
  				<b-tab id="studenttab" active :title="$ml.get('student')">
				  	<b-form v-on:submit.prevent="createStudent">
				  		<b-row>
				  			<b-col>
				  				<label v-text="$ml.get('username')"></label>
				  				<b-form-input type="text" v-model="student.user_name" :placeholder="$ml.get('username')"></b-form-input>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('password')"></label>
				  				<b-form-input type="password" v-model="student.password" :placeholder="$ml.get('password')"></b-form-input>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('programme')"></label>
				  				<b-form-select v-model="student.programme" :options="programmes" value-field="_id" text-field="name" disabled-field="disabled" :placeholder="$ml.get('programme')"></b-form-select>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('academicterm')"></label>
				  				<b-form-select v-model="student.academic_term" :options="academic_terms" value-field="_id" text-field="name" disabled-field="disabled" :placeholder="$ml.get('academicterm')"></b-form-select>
				  			</b-col>
				  		</b-row>
				  		<b-row>
				  			<b-col>
				  				<label v-text="$ml.get('name')"></label>
				  				<b-form-input type="text" v-model="student.name" :placeholder="$ml.get('name')"></b-form-input>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('email')"></label>
				  				<b-input-group prepend="@">
					  				<b-form-input type="email" v-model="student.email" :placeholder="$ml.get('email')"></b-form-input>
				  				</b-input-group>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('phone')"></label>
				  				<b-form-input type="number" v-model="student.phone" :state="validatePhone" :placeholder="$ml.get('phone')"></b-form-input>
				  				<b-form-invalid-feedback :state="validatePhone" v-text="$ml.get('errorphone')">
							    </b-form-invalid-feedback>
				  			</b-col>
				  		</b-row>
				  		<b-row>
				  			<b-col>
				  					<b-row>
				  						<label v-text="$ml.get('fathersname')"></label>
				  				        <b-form-input type="text" v-model="student.father_name" :placeholder="$ml.get('fathersname')"></b-form-input>
				  					</b-row>
				  					<b-row>
				  				         <label v-text="$ml.get('fathercontact')"></label>
				  				           <b-form-input type="number" v-model="student.father_contact"  :state="validateFatherPhone" :placeholder="$ml.get('fathercontact')"></b-form-input>
				  				           <b-form-invalid-feedback :state="validateFatherPhone" v-text="$ml.get('errorphone')"></b-form-invalid-feedback>
				  					</b-row>
				  			</b-col>

				  			<b-col>
				  					<b-row>
				  						<b-col>
				  							<label v-text="$ml.get('mothersname')"></label>
				  				        <b-form-input type="text" v-model="student.mother_name" :placeholder="$ml.get('mothersname')"></b-form-input>
				  						</b-col>
				  					</b-row>
				  					<b-row>
				  						<b-col>
				  							<label v-text="$ml.get('mothercontact')"></label>
				  				        <b-form-input type="number" v-model="student.mother_contact" :state="validateMotherPhone" :placeholder="$ml.get('mothercontact')"></b-form-input>
				  				        <b-form-invalid-feedback :state="validateMotherPhone" v-text="$ml.get('errorphone')"></b-form-invalid-feedback>
				  						</b-col>
				  					</b-row>
				  			</b-col>


				  			<b-col>
				  				<label v-text="$ml.get('profilepic')"></label>
				  				<b-form-file v-model="student.profile_pic" :placeholder="$ml.get('profilepic')"></b-form-file>
				  			</b-col>
				  		</b-row>
				  		<b-row>
				  			<b-col>
				  				<label v-text="$ml.get('currentaddress')"></label>
				  				<b-form-textarea v-model="student.current_address" :placeholder="$ml.get('currentaddress')"></b-form-textarea>
				  			</b-col>
				  			<b-col>
				  				<label v-text="$ml.get('corresaddress')"></label>
				  				<b-form-textarea v-model="student.correspondence_address" :placeholder="$ml.get('corresaddress')"></b-form-textarea>
				  				<b-form-checkbox @change="copyAddr" switch size="sm" v-model="copyAddress" value="true" unchecked-value="false">Same as Current Address</b-form-checkbox>
				  			</b-col>
				  		</b-row>
				  		<br>
				  		<div class="footer">
				  			<b-button pill variant="primary" type="submit" v-text="$ml.get('submit')"></b-button>
				  		</div>
				  	</b-form>
				</b-tab>
		  	</b-tabs>
  	</b-card>
  </div>
</template>
	
<script>

	//Imports
	import {acadUrl} from '@/apiUrl'
	import axios from 'axios'
	import Vue from 'vue'
	import VueNotifications from 'vue-notifications'
	import miniToastr from 'mini-toastr' 

	//Declarations
    var api = axios.create({
	  withCredentials :true
	})
	const toastTypes = {
	  success: 'success',
	  error: 'error',
	  info: 'info',
	  warn: 'warn'
	}
	const options = {
	  success: toast,
	  error: toast,
	  info: toast,
	  warn: toast
	}
	function toast ({title, message, type, timeout, cb}) {
	  return miniToastr[type](message, title, timeout, cb)
	}

	//Function Calls
	miniToastr.init({types: toastTypes})
	Vue.use(VueNotifications, options)

    //Vue Handler
	export default {
		name:"AddStudent",
		components:{

		},
		data : function () {
			return {
				//form data
				student : {
					phone:'',
					mother_contact:'',
					father_contact:''
				},
				copyAddress:false,
				//Programme List
				programmes : [{name:"No Programmes",_id:"0",disabled:true}],
				//Academic-Term List
				academic_terms : [{name:"No Academic-Terms",_id:"0",disabled:true}]
			}
		},
		methods:{
			// Send Create Request
			createStudent(args) {
				console.log(this.student)
				var studentformdata = new FormData()
				if(this.student.programme) {
					studentformdata.append('programme',this.student.programme)

				}
				if(this.student.academic_term) {
					studentformdata.append('academic_term',this.student.academic_term)
				}
				studentformdata.append('user_name',this.student.user_name)
				studentformdata.append('password',this.student.password)
				studentformdata.append('name',this.student.name)
				studentformdata.append('email',this.student.email)
				studentformdata.append('phone',this.student.phone)
				studentformdata.append('profile_pic',this.student.profile_pic)
				studentformdata.append('father_name',this.student.father_name)
				studentformdata.append('father_contact',this.student.father_contact)
				studentformdata.append('mother_name',this.student.mother_name)
				studentformdata.append('mother_contact',this.student.mother_contact)
				studentformdata.append('current_address',this.student.current_address)
				studentformdata.append('correspondence_address',this.student.correspondence_address)
				api.post(`${acadUrl}stud/student/create`,studentformdata,{headers:{'Content-Type':'multipart/form-data'}}).then((student) => {
					console.log(student.data)
					this.$router.push({name:'Student List'});
				})
			},
			//Copy address
			copyAddr(args) {
				if(args=="true") {
					this.student.correspondence_address = this.student.current_address
				}
			}
		},
		mounted(){

		},
		computed: {
			// Validate Fields
			validatePhone() {
				return this.student.phone.length==10 && this.student.phone>0
			},
			validateMotherPhone() {
				return this.student.mother_contact.length==10 && this.student.mother_contact>0
			},
			validateFatherPhone() {
				return this.student.father_contact.length==10 && this.student.father_contact>0
			}
		}
	};
</script>

<style scoped>
	#studentform {
		min-height: calc( 100vh - 92px)
	}
	.card-body {
		padding: .5rem
	}
	#studenttab {
		min-height: calc( 100vh - 150px)
	}
	.form-control:focus {
    color: #5c6873;
    background-color: #fff;
     border-color: #8ad4ee; 
    outline: 0;
     -webkit-box-shadow: 0 0 0 0.0 rgba(32, 168, 216, 0.25) !important;
    box-shadow: 0 0 0 0.0 rgba(32, 168, 216, 0.25) !important;
}
.custom-select:focus {
    border-color: #8ad4ee;
    outline: 0;
    -webkit-box-shadow: 0 0 0 0 rgba(32, 168, 216, 0.25) !important;
    box-shadow: 0 0 0 0 rgba(32, 168, 216, 0.25) !important;
}
</style>